import React, {useState, useEffect} from 'react'
//import NameForm from './components/NameForm'
import competitionService from './services/CompetitionService'

const App = () => {
  const [name, setName] = useState('')
  const [winners, setWinners] = useState([])
  const [message, setMessage] = useState('Aloita peli...')
  //const [number, setNumber] = useState(0)

  /*useEffect(() => {
    competitionService
      .getNumber()
      .then(n => {
        setNumber(n.number)
      })
  }, [])*/

  useEffect(() => {
    competitionService
      .getWinners()
      .then(winners => {
        setWinners(winners.reverse())
      })
  }, [])

  const handleNameChange = (event) => {
    setName(event.target.value)
    console.log(name)
  }

  const handleClick = () => {
      /*competitionService
        .increment(name)
        .then(note => {
          setNumber(note.number)
          console.log(note.message)
        })*/

      competitionService
        .increment(name)
        .then(note => {
          setMessage(note.message)
        })

      competitionService
        .getWinners()
        .then(winners => {
          setWinners(winners.reverse())
        })
  }

  return(
    <div>
      
      <div>
        <p>Nappipeli!</p>
      </div>

      <div>
        <p>{message}</p>
      </div>
      
      <div>
        <form>
          Syötä nimimerkki<input
            value={name}
            onChange={handleNameChange}/>
        </form>
      </div>

      <div>
        <p>Paina nappia voittaaksesi!</p>
        <div>
          <button onClick={handleClick}>        plus      </button>    
        </div>
      </div>

      <div>
        <p>Edelliset voittajat:</p>
        <ul>
          {winners.map(winner => 
            <li key={winner.id}>{winner.name}</li>  
          )}
        </ul>
      </div>
    
    </div>
  )
}

export default App;
